import React, { Component } from 'react'
import SearchBar from './SearchBar';
import AllFilter from './AllFilter';
import Sortby from './Sortby.js';
import TitleCard from './TitleCards';
import DailyQuestionCard from './DailyQuestionCard';
import { getQuestions } from '../../api_code/GetFromBackend'
import "./MainPage.css";

class MainPage extends Component {
    constructor(props){
        super(props);
        this.state={
        items:[]
        };
        this.titles = {
            titles:
            [{name:"Difficulty", top:"hard", mid:"medium", bot:"easy"},
             {name:"Status", top:"Todo", mid:"Solved", bot:"Attempted"}]
        }
    
    }
    
    componentDidMount() {
        console.log('ee')
        let questions = getQuestions()
        questions.then((value) => {
            this.setState({
               items: value
            })
        })
    }
    render() {
        return (
            <div>
                <h1 className="question-of-the-day-title">Question of the DAYYY</h1>
                <div className="question-of-the-day-card">
                    <DailyQuestionCard items={{ "id": 2, "title": "Longest Substring Without Repeating Characters", "description": "Given a string s, find the length of the longest substring without repeating characters." }} class="card" />
                </div>
                <div className="search-bar"><SearchBar></SearchBar></div>
                <div class="row" className="filter-rows">
                <div class="block" className="difficulty-filter"><AllFilter titles={this.titles.titles[0]}></AllFilter></div>
                <div class="block" className="status-filter"><AllFilter titles={this.titles.titles[1]}></AllFilter></div>
                <div class="block" className="sortby-filter"><Sortby></Sortby></div>
                </div>
                {/* {this.titles.titles.map(obj => <div style={{marginLeft: "10.5%"}}><AllFilter titles={obj} class="card"/></div>)} */}
                {this.state.items.map(obj => <div className="question-card"><TitleCard items={obj} class="card"/></div>)}
            </div>
        
        )
    }
}
export default MainPage;