export const getQuestions = () => {
    let questions = [
        { id: 1, title:  "Find First and Last Position of Element in Sorted Array", desc: "Given an array of integers nums sorted in ascending order, find the starting and ending position of a given target value.If target is not found in the array, return [-1, -1]." },
        { id: 2, title: "Permutations", desc: "Given a collection of numbers, nums, that might contain duplicates, return all possible unique permutations in any order." },
        { id: 3, title: "Swap Nodes in Pairs", desc: "Given a linked list, swap every two adjacent nodes and return its head. You may not modify the values in the list's nodes. Only nodes itself may be changed." }]
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve(questions);
        }, 1000)
    })
}